import getIsDesktop from "../common/lib/getIsDesktop";

const state = {
  IsDesktopPlatform: getIsDesktop()
};

const getters = {
  //    
  getIsDesktopPlatform: state => {
    return state.isDesktopPlatform;
  }
};

const mutations = {
  setPlatform: (state, isDesktop) => {
    state.isDesktopPlatform = isDesktop;
  }
};

export default {
  namespaced: true,
  state,
  getters,
  mutations
};
